body{
  background-color: lightgoldenrodyellow;
}

.cstmnav{
  background-color: rgb(254, 238, 0);
}

.secondnav{
  width: 100%;
  background-color: rgb(241, 241, 237);
  border-bottom: 1px solid rgb(226, 229, 241);
  height: 45px;
  display: flex;
  justify-content: space-around;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight:700;
  font-size: 12.5px;
}

.secondnav ul{
  display: flex;
  margin-top: 10px;
}

.secondnav ul li{
  list-style: none;
  margin: 0 20px;
  cursor: pointer;
}

.rndbar{
  width: 100%;
  display: flex;
  padding: 20px 0;
  justify-content: center;
}

.rndbar img{
  width: 7%;
  margin: 0 5px;
  cursor: pointer;
}

.cstm-card{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.cstmcard:hover{
  box-shadow: 0 2px 10px lightgray;
}

.cstmcard{
  margin: 1% 0;
  height: 435px;
}
.cstmcard img{
  width: 55%;
  margin: 0 auto;
  cursor: pointer;
}

.banner{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  cursor: pointer;
}

.flist ul li{
  list-style: none;
  cursor: pointer;
  font-size: 13px;
}

.flist ul li:hover{
  text-decoration: underline;
}

.ftrlist{
  border-bottom: 1px solid gray;
  border-top: 1px solid gray;
}

.app-icon{
  width: 100%;
  margin: 25px 0;
  display: flex;
  justify-content: center;
}

.a-icon{
  width: 12%;
margin-left: 10px;
cursor: pointer;
}

.ftr-icon{
  width: 2.7%;
  height: 30px;
  margin-left: 8px;
  cursor: pointer;
}

.btmlist{
  background-color: rgb(241, 241, 237);
  color: black;
  display: flex;
  justify-content: space-around;
  font-size: 12px;
}

.btmlist ul{
  display: flex;
}

.btmlist ul li{
  list-style: none;
  margin: 0 10px;
  cursor: pointer;
}

.btmlist ul li:hover{
  text-decoration: underline;
}